html {
  height: 100%;
}
body {
  position: relative;
  min-height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  padding-bottom: 50px;
}
.app {
  min-height: 100%;
  min-width: 100vw;
  height: 100%;
  width: 100%;
}
[draggable] {
  user-select: none;
}
.container {
  position: relative;
  min-height: 100%;
  height: 100%;
  max-width: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.pane-container {
  padding: 25px 10px;
}
@media screen and (min-width: 768px) {
.pane-container {
    flex-direction: row;
}
}
.pane {
  max-width: 980px;
  min-width: 100%;
  width: 100%;
  padding: 10px;
}
.pane:first-child {
    padding-top: 0;
}
.pane:last-child {
    padding-bottom: 0;
}
@media screen and (min-width: 768px) {
.pane {
    min-width: 50%;
    padding: 0 10px;
}
}
#Editor > .header {
  display: flex;
  justify-content: space-between;
  padding: 9px 10px 10px;
  margin: 0;
  font-size: 14px;
  line-height: 17px;
  background-color: #f6f8fa;
  border: 1px solid rgba(27, 31, 35, 0.15);
  border-bottom: 0;
  border-radius: 3px 3px 0 0;
}
#Editor > .src {
  padding: 15px;
  word-wrap: break-word;
  background-color: #fff;
  border: 1px solid #ddd;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  outline: none;
}
#Editor textarea {
  box-sizing: content-box;
  font-family: monospace;
  white-space: pre-line;
  outline: none;
  border: none;
  resize: none;
  width: 100%;
  overflow: none !important;
  height: 100%;
  min-height: 50px;
}
#Markdown > .header {
  display: flex;
  justify-content: space-between;
  padding: 9px 10px 10px;
  margin: 0;
  font-size: 14px;
  line-height: 17px;
  background-color: #f6f8fa;
  border: 1px solid rgba(27, 31, 35, 0.15);
  border-bottom: 0;
  border-radius: 3px 3px 0 0;
}
#Markdown > .src {
  padding: 45px;
  word-wrap: break-word;
  background-color: #fff;
  border: 1px solid #ddd;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  outline: none;
}
input.filename {
  border: none;
  outline: none;
  background-color: transparent;
  width: 200px;
}
.pane > .header > .header-controls * {
  margin: 0 3px;
}
.pane > .header > .header-controls *:first-child {
    margin-left: 0;
}
.pane > .header > .header-controls *:last-child {
    margin-right: 0;
}
